@import '../../styles/utils/mixins';
@import '../../styles/utils/vars';

.product-card {
  box-sizing: border-box;
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 32px;
  gap: 8px;
  width: 212px;
  height: 439px;
  
  background: #FFF;
  border: 1px solid $element-color;

  @include onTablet {
    width: 237px;
    height: 512px;
  }

  @include onDesktop {
    width: 272px;
  }

  &__image {
     &-container {
      display: flex;
      justify-content: center;
      flex-grow: 1;
      height: 110px;
      object-fit: contain;
      margin-bottom: 24px;

      transition: transform 0.3s;

    @include onTablet {
      height: 194px;
      width: 174px;
    }

    @include onDesktop {
      width: 208px; 
    }
      &:hover {
        transform: scale(1.1);
      }
    }
  }

  &__title {
    &-container {
      text-decoration: none;
    }

    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    margin: 0;
    margin-bottom: 8px;
    color: $primary-color;
  }

  &__price {
    display: inline-block;
    margin: 0;
    margin-right: 10px;

    &-container {
      font-weight: 800;
      font-size: 22px;
      line-height: 140%;

      margin: 0;
      margin-bottom: 24px;
      color: $primary-color;

      &::after {
        display: block;
        content: "";
        margin-top: 8px;
        border-bottom: 1px solid $element-color;
      }
    }
  }

  &__full-price {
    display: inline-block;
    margin: 0;

    font-weight: 600;
    color: $secondary-color;
    text-decoration: line-through;

    &--isBrandNewPhones {
      display: none;
    }
  }

  &__details {
    margin-bottom: 16px;
  }

  &__description {
    display: flex;
    justify-content: space-between;

    font-weight: 700;
    font-size: 12px;
    line-height: 15px;

    margin: 0;
    margin-bottom: 8px;
    color: $secondary-color;

    &-title {
      margin: 0;    
    }

    &-content {
      color: $primary-color;
      margin: 0;
    }
  }
}

